* {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", "Helvetica Neue", Arial, sans-serif;
  color: #4a4a4a;
  padding-bottom: 20px;
}

button {
  border: none;
  background: transparent;
  outline: none;
  cursor: pointer;
  touch-action: manipulation;
  user-select: none;
}

a {
  color: #00adcb;
  text-decoration: none;
  cursor: pointer;

  &:focus,
  &:hover {
    color: #006c7f;
    text-decoration: underline;
  }
}
