
.bounceLoading {
  width: 100%;
  height: calc(100% - 50px);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 9000;
  background: white;
}

.background {
  width: 90px;
  height: 90px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: #fff;
  border-radius: 10px;

  &::before,
  &::after {
    content: '';
    display: block;
    width: 60px;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background: #fbc33e;
    border-radius: 50%;
    animation: loading-bounce 2s infinite ease-in-out;
    opacity: 0.5;
  }

  &::after {
    animation-delay: -1s;
  }
}

.contains {
  position: absolute;
  height: 100%;
  top: 0;
  z-index: 1;

  .background {
    width: 100%;
    height: 100%;

    &::after,
    &::before {
      width: 50%;
      height: 50%;
    }
  }
}

@keyframes loading-bounce {
  0%,
  100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}
