.navbarToggle {
  padding: 5px 10px;
  width: 44px;
  height: 34px;
  background-color: transparent;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  border-color: #ddd;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  &:hover,
  &:focus {
    background-color: #ddd;
  }
}

.element {
  display: block;
  height: 2px;
  border-radius: 1px;
  background-color: #888;
  width: 100%;
}
