.header {
  position: sticky;
  top: 0;
  width: 100%;
  height: 50px;
  background-color: #4a4a4a;
  z-index: 3;
}

.content {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.linkContainer {
  display: flex;
  height: 100%;
}

.link {
  padding: 15px;
  color: white;
  font-size: 14px;
  line-height: 20px;
  text-align: start;

  &:hover,
  &.active {
    color: white;
    text-decoration: none;
  }

  &:hover {
    background-color: #5b5b5b;
  }

  &.active {
    background-color: #393939;
  }
}

.toggle {
  display: none;
}

@media (max-width: 768px) {
  .linkContainer {
    display: none;

    &.show {
      position: absolute;
      top: 50px;
      height: auto;
      display: flex;
      width: 100%;
      background-color: #4a4a4a;
      left: 0px;
      flex-direction: column;
    }
  }

  .link {
    padding: 10px;
  }

  .toggle {
    display: flex;
  }
}
