.modal {
  min-width: 700px;
  min-height: 150px;

  @media (max-width: 768px) {
    width: 90%;
    min-width: auto;
  }

  @media (max-width: 480px) {
    width: 95%;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid #efefef;
}

.title {
  margin: 0;
}

.close {
  &:hover {
    svg {
      fill: #777;
    }
  }
}

.content {
  padding: 16px;
}

.controls {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  width: 120px;
  margin-left: auto;
}
