.button {
  display: inline-block;
  padding: 4px 16px 6px;
  border: 1px solid #d6d6d6;
  border-radius: 50px;
  font-size: 13px;
  font-weight: 700;
  line-height: 1.4;
  color: #4a4a4a;
  background-color: #fff;

  &:not(:disabled):hover {
    color: #4a4a4a;
    background-color: #e6e6e6;
    border-color: #b8b8b8;
  }

  &:disabled {
    opacity: 0.5;
    cursor: default;
  }
}

.primary {
  border-color: #fab30c;
  background-color: #fbc33e;

  &:not(:disabled):hover {
    background-color: #fab30c;
    border-color: #c58c04;
  }
}

.link {
  padding: 0;
  border: none;
  font-size: 16px;
  font-weight: normal;
  text-decoration: none;
  color: #00adcb;
  background-color: transparent;

  &:not(:disabled):hover {
    color: #006c7f;
    text-decoration: underline;
    background-color: transparent;
  }
}
